.board {
  display: grid;
  grid-template-columns: repeat(8, 50px);
  grid-template-rows: repeat(8, 50px);
  margin: 20px auto;
  width: 400px;
}

.board-row {
  display: contents;
}

.square {
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.square.light {
  background-color: #f0d9b5;
}

.square.dark {
  background-color: #b58863;
}

.square.red {
  background-color: red;
  border-radius: 50%;
}

.square.black {
  background-color: black;
  border-radius: 50%;
}
